.cart-items{
    margin: 30px auto;
    width: 50%;
    font-size: 20px;
    background-color: rgb(171, 218, 233);
    border: 2px solid black;
    border-radius: 5px;
}

.cart-items-header{
    display: flex;
    justify-content: space-around;
    align-items: center;
    height: 50px;
    border-bottom: 2px solid black;
}

.cart-items-empty{
    display: flex;
    justify-content: center;
    align-content: center;
    font-size: 18px;
    font-weight: 700;
    padding-top: 20px;
    padding-bottom: 20px;

}
.cart-items-image{
    width: 100px;
    height: auto;
    border: 2px solid black;
    margin-right: 10px;
}

.cart-items-list{
    display: flex;
    align-items:center ;
    padding-top: 20px;
    padding-left: 20px;
    padding-bottom: 20px;
    font-size: 20px;
    font-weight: 700;
    margin-left: 5%;
}

.cart-items-name,
.cart-items-function{
    width: 25%;
}

.cart-items-add,
.cart-items-remove{
    width: 30px;
    margin-right: 15px;
    font-weight: 700;
    border: none;
    border-radius: 3px;
    font-size: 18px;
    color: white;
    cursor: pointer;
}

.cart-items-add{
    background-color: red;
}

.cart-items-remove{
    background-color: green;
}
.cart-items-add:active{
    transform:  translateY(1.2px);
}

.cart-items-remove:active{
    transform: translateY(1.2px);
}

.cart-items-total-price-name{
    display: flex;
    padding-top: 20px;
    padding-bottom: 20px;
    padding-left: 22%;
    font-size: 20px;
    font-weight: 700;
    border-top: 2px solid black;
}

.cart-items-total-price{
    display: flex;
    padding-left: 45%;
}

.clear-cart{
    display: flex;
    justify-content: flex-end;
    margin-top: 10px;
    margin-bottom: 10px;
}
.clear-cart-button{
    border: none;
    border-radius: 5px;
    background-color: red;
    height: 30px;
    width: 100px;
    font-size: 18px;
    color: white;
    cursor: pointer;
}
.clear-cart-button:hover{
 background-color: violet;
}
